import { useState, useEffect } from "react";
import { observer } from "mobx-react-lite";
import { Button } from "antd";
import { CaretRightOutlined, PauseOutlined } from "@ant-design/icons";
import store from "./Store2";

const Clock = observer(
  ({
    id,
    duration,
    isButtonDisabled,
  }: {
    id: string;
    duration: number;
    isButtonDisabled: boolean;
  }) => {
    const clock = duration;
    const [clockEnabled, setClockEnabled] = useState(false);

    useEffect(() => {
      if (!clockEnabled || isButtonDisabled) {
        return;
      }
      const timerId = setInterval(() => {
        store.store.changeRealDuration(id, 1);
      }, 1000);
      return () => {
        clearInterval(timerId);
      };
    }, [id, clockEnabled, isButtonDisabled]);

    let minutes;
    if (Math.floor(clock / 60) < 10) {
      minutes = "0" + Math.floor(clock / 60);
    } else {
      minutes = Math.floor(clock / 60);
    }

    let seconds;
    if (clock - Math.floor(clock / 60) * 60 < 10) {
      seconds = "0" + (clock - Math.floor(clock / 60) * 60);
    } else {
      seconds = clock - Math.floor(clock / 60) * 60;
    }

    const res = minutes + ":" + seconds;

    return (
      <div className="clock" style={{ width: "75px" }}>
        <div className="tableElem">{res}</div>
        <div className="tableElem">
          <Button
            size="small"
            disabled={isButtonDisabled}
            onClick={() => {
              setClockEnabled(!clockEnabled);
              if (clockEnabled) {
                store.store.updateActionCard(id);
              }
            }}
            icon={!clockEnabled ? <CaretRightOutlined /> : <PauseOutlined />}
          ></Button>
        </div>
      </div>
    );
  }
);

export default Clock;
