import { Table, Input, Select, Button } from "antd";
import "./App.css";
import store from "./Store2";
import { ActionType } from "./Store";
import { observer } from "mobx-react-lite";

interface Date {
  date: string;
}

const ActionForm = observer(({ date }: Date) => {
  const list = store.store.actionCards.filter((action) => action.date === date);
  const isResEmpty = list.length === 0;
  const { Option } = Select;

  const columns = [
    {
      title: "Plans:",
      dataIndex: "plans",
      key: "plans",
    },
    {
      title: "Duration:",
      dataIndex: "duration",
      key: "duration",
    },
    {
      title: "Category:",
      dataIndex: "category",
      key: "category",
    },
    {
      title: "",
      dataIndex: "remove",
      key: "remove",
    },
  ];

  const data = list.map((card) => {
    return {
      key: card.id,
      plans: (
        <Input
          style={
            card.isWork
              ? {
                  maxWidth: "150px",
                  backgroundColor: "rgba(142, 202, 230)",
                }
              : {
                  maxWidth: "150px",
                  backgroundColor: "rgba(255, 183, 3)",
                }
          }
          defaultValue={card.actionName}
          onChange={(event) =>
            store.store.changeActionName(card.id, event.target.value)
          }
        />
      ),
      duration: (
        <Input
          style={{
            maxWidth: "60px",
          }}
          defaultValue={card.expectedDuration}
          onChange={(event) =>
            store.store.changeExpectedDuration(card.id, event.target.value)
          }
        />
      ),
      category: (
        <Select
          style={{
            width: "88px",
          }}
          defaultValue={card.isWork ? ActionType.Work : ActionType.Rest}
          onChange={(value) =>
            store.store.changeIsWork(
              card.id,
              value === ActionType.Work ? true : false
            )
          }
        >
          <Option value={ActionType.Work}>{ActionType.Work}</Option>
          <Option value={ActionType.Rest}>{ActionType.Rest}</Option>
        </Select>
      ),
      remove: (
        <Button onClick={() => store.store.removeActionCard(card.id)}>
          Remove
        </Button>
      ),
    };
  });

  return (
    <>
      {isResEmpty ? (
        <div>
          <p className="noTasks">No plans for the day</p>
        </div>
      ) : (
        <>
          <Table
            bordered={false}
            size="small"
            columns={columns}
            dataSource={data}
            style={{
              height: "235px",
            }}
            pagination={{
              pageSize: 4,
              hideOnSinglePage: true,
              position: ["bottomCenter"],
            }}
          />
        </>
      )}
    </>
  );
});

export default ActionForm;
