import "./App.css";
import ActionList from "./ActionList";
import moment from "moment";
import "moment/locale/ru";
import { observer } from "mobx-react-lite";
import { Carousel } from "antd";
import MemoryOverlay from "./MemoryOverlay";
import EmotionChart from "./EmotionChart";
import DonePlansChart from "./DonePlansChart";

const MyDay = observer(() => {
  const stringDate = moment(new Date()).format("dddd (D MMMM YYYY)");

  return (
    <div className="mainBlock_myDay">
      <div className="mainBlock_myDay_plans">
        <div>
          <h1 style={{ color: "#023047" }}>{stringDate}</h1>
        </div>
        <div>
          <ActionList />
        </div>
      </div>
      <div className="mainBlock_myDay_charts">
        <div className="myDay_charts_container">
          <Carousel>
            <EmotionChart date={moment().format("YYYY-MM-DD")} />
            <DonePlansChart date={moment().format("YYYY-MM-DD")} />
          </Carousel>
        </div>
        <MemoryOverlay />
      </div>
    </div>
  );
});

export default MyDay;
