import { observer } from "mobx-react-lite";
import "./App.css";
import store from "./Store2";
import { Chart } from "react-chartjs-2";
import { Carousel, Card } from "antd";

function WeeklyReport({ days }: { days: number }) {
  function PrepareDataChart() {
    let expectedDur = 0;
    let realDur = 0;
    store.store.actionCardsForSomePeriod(days).map((action) => {
      const expectedDurArr = action.expectedDuration.split(":");
      expectedDur += +expectedDurArr[0] * 3600 + +expectedDurArr[1] * 60;
      realDur += action.realDuration;
      return realDur;
    });
    let unDone = 100;
    let done = (realDur * 100) / expectedDur;
    if (realDur !== 0 || done > 1) {
      unDone = 100 - done;
    }
    return [done, unDone];
  }

  const data1 = PrepareDataChart();

  const chartData1 = {
    labels: ["Done (%)", "To be done (%)"],
    datasets: [
      {
        id: 1,
        label: `${store.store.actionCards.length}`,
        data: data1,
        backgroundColor: ["rgba(255, 183, 3, 0.8)", "rgba(243, 246, 224, 1)"],
        borderColor: ["black", "black"],
        borderWidth: 0.2,
      },
    ],
  };

  let stringEmotion;
  function prepareEmotionChart() {
    const badEmotionCards = store.store
      .actionCardsForSomePeriod(days)
      .filter((actionCard) => actionCard.emotion === "");
    const middleEmotionCards = store.store
      .actionCardsForSomePeriod(days)
      .filter((actionCard) => actionCard.emotion === "");
    const goodEmotionCards = store.store
      .actionCardsForSomePeriod(days)
      .filter((actionCard) => actionCard.emotion === "");

    let badEmotion = 0;
    badEmotionCards.map((actionCard) => {
      badEmotion += actionCard.realDuration / 60;
      return badEmotion;
    });

    let middleEmotion = 0;
    middleEmotionCards.map((actionCard) => {
      middleEmotion += actionCard.realDuration / 60;
      return middleEmotion;
    });

    let goodEmotion = 0;
    goodEmotionCards.map((actionCard) => {
      goodEmotion += actionCard.realDuration / 60;
      return goodEmotion;
    });

    let background = 0;
    if (badEmotion === 0 && middleEmotion === 0 && goodEmotion === 0) {
      background = 100;
    }

    const arr = [goodEmotion, middleEmotion, badEmotion, background];

    if (goodEmotion > middleEmotion && goodEmotion > badEmotion) {
      stringEmotion = "excellent";
    } else if (middleEmotion > goodEmotion && middleEmotion > badEmotion) {
      stringEmotion = "good";
    } else if (badEmotion > goodEmotion && badEmotion > middleEmotion) {
      stringEmotion = "badly";
    }
    return arr;
  }

  const chartData2 = {
    labels: [" (min)", " (min)", " (min)"],
    datasets: [
      {
        id: 1,
        label: `${store.store.actionCards.length}`,
        data: prepareEmotionChart(),
        backgroundColor: [
          "rgba(167, 201, 87, 1)",
          "rgba(255, 223, 82, 1)",
          "rgba(188, 71, 73, 1)",
          "rgba(243, 246, 224, 0.5)",
        ],
        borderColor: ["black", "black", "black"],
        borderWidth: 0.2,
      },
    ],
  };

  let stringCategory;
  function prepareCategoryChart() {
    const workActionCards = store.store
      .actionCardsForSomePeriod(days)
      .filter((actionCard) => actionCard.isWork === true);
    const notWorkActionCards = store.store
      .actionCardsForSomePeriod(days)
      .filter((actionCard) => actionCard.isWork === false);

    let totalMins1 = 0;
    workActionCards.map((actionCard) => {
      totalMins1 += actionCard.realDuration / 60;
      return totalMins1;
    });

    let totalMins2 = 0;
    notWorkActionCards.map((actionCard) => {
      totalMins2 += actionCard.realDuration / 60;
      return totalMins2;
    });

    if (totalMins1 > totalMins2) {
      stringCategory = '"Work"';
    } else if (totalMins1 < totalMins2) {
      stringCategory = '"Leisure"';
    } else {
      stringCategory = "";
    }

    let background = 0;
    if (totalMins1 === 0 && totalMins2 === 0) {
      background = 100;
    }

    const finalArr = [totalMins1, totalMins2, background];
    return finalArr;
  }

  const chartData3 = {
    labels: ["Work (mins)", "Leisure (mins)"],
    datasets: [
      {
        id: 1,
        label: `${store.store.actionCards.length}`,
        data: prepareCategoryChart(),
        backgroundColor: [
          "rgba(142, 202, 230)",
          "rgba(255, 183, 3, 0.8)",
          "rgba(243, 246, 224, 0.5)",
        ],
        borderColor: ["black", "black"],
        borderWidth: 0.2,
      },
    ],
  };

  return (
    <div className="statistics_report_container">
      <Carousel>
        <div className="statistics_report_card">
          <Card
            style={{
              backgroundColor: "#023047",
              color: "rgba(255, 183, 3)",
              fontWeight: "bold",
            }}
          >
            <Chart
              datasetIdKey={`${store.store.actionCards.length}`}
              type="pie"
              data={chartData3}
              options={{
                color: "rgba(255, 183, 3)",
                font: {
                  family: "PTSansRegular",
                },
              }}
            />
            <p>Mostly you performed tasks from the category {stringCategory}</p>
          </Card>
        </div>

        <div className="statistics_report_card">
          <Card
            style={{
              backgroundColor: "#023047",
              color: "rgba(255, 183, 3)",
              fontWeight: "bold",
            }}
          >
            <Chart
              datasetIdKey={`${store.store.actionCards.length}`}
              type="pie"
              data={chartData2}
              options={{
                color: "rgba(255, 183, 3)",
                font: {
                  family: "PTSansRegular",
                },
              }}
            />
            <p>Mostly completing tasks you felt {stringEmotion}</p>
          </Card>
        </div>

        <div className="statistics_report_card">
          <Card
            style={{
              backgroundColor: "#023047",
              color: "rgba(255, 183, 3)",
              fontWeight: "bold",
              minHeight: "354px",
            }}
          >
            <Chart
              datasetIdKey={`${store.store.actionCards.length}`}
              type="pie"
              data={chartData1}
              options={{
                color: "rgba(255, 183, 3)",
                font: {
                  family: "PTSansRegular",
                },
              }}
            />
            <p>
              Your tasks were completed by{" "}
              {isNaN(Math.floor(data1[0])) ? "0" : Math.floor(data1[0])}%
            </p>
          </Card>
        </div>
      </Carousel>
    </div>
  );
}

export default observer(WeeklyReport);
