import { useState } from "react";
import { Input, Form, Select, Button, Dropdown, Menu, TimePicker } from "antd";
import { EllipsisOutlined } from "@ant-design/icons";
import "./App.css";
import { DatePicker } from "@blueprintjs/datetime";
import moment from "moment";
import MomentLocaleUtils from "react-day-picker/moment";
import "moment/locale/ru";
import { nanoid } from "nanoid";
import { ActionType } from "./Store";
import store from "./Store2";
import ActionForm from "./ActionForm";
import PlanningChart from "./PlanningChart";
import MemoryOverlay from "./MemoryOverlay";
import { observer } from "mobx-react-lite";
import MenuItem from "antd/lib/menu/MenuItem";

moment.locale("en");

const Planning = observer(() => {
  const [form] = Form.useForm();
  const { Option } = Select;
  const [val, setVal] = useState(new Date());
  const stringDate = moment(val).format("dddd (D MMMM YYYY)");
  const currentDate = moment(val).format("YYYY-MM-DD");
  const initActionForm = {
    id: nanoid(),
    actionName: "",
    expectedDuration: "",
    isWork: true,
    date: currentDate,
    realDuration: 0,
    emotion: 0,
    isDone: false,
  };

  const [adder, setAdder] = useState([initActionForm]);
  const format = "HH:mm";
  const menu = (
    <Menu>
      <MenuItem>
        <p
          onClick={() => {
            setAdder(adder.filter((action) => action.date !== currentDate));
            store.store.removeDatedActionCards(currentDate);
          }}
          className="text"
        >
          Delete the plans for the day
        </p>
      </MenuItem>
      <MenuItem>
        <p
          onClick={() => {
            setAdder([initActionForm]);
            store.store.removeAllActionCards();
          }}
          className="text"
        >
          Delete all plans
        </p>
      </MenuItem>
    </Menu>
  );

  return (
    <>
      <div className="space">
        <div className="block_date_and_icon">
          <div className="block_date">
            <h1 className="date">{stringDate}</h1>
          </div>
          <div className="block_icon">
            <Dropdown overlay={menu} placement="bottomLeft" trigger={["click"]}>
              <Button
                icon={<EllipsisOutlined />}
                style={{ border: "0px solid transparent" }}
              />
            </Dropdown>
          </div>
        </div>
      </div>
      <div className="mainBlock">
        <div className="block_calendar">
          <DatePicker
            className="calendar"
            value={val}
            onChange={(date) => setVal(date)}
            locale="en"
            localeUtils={MomentLocaleUtils}
          />
        </div>
        <div className="block_plans">
          <ActionForm date={currentDate} />
        </div>
        <div className="block_chart">
          <PlanningChart date={currentDate} />
        </div>
      </div>
      <div className="footer">
        <div className="footer_form">
          <Form
            form={form}
            layout="inline"
            onFinish={(values) => {
              setAdder([
                ...adder,
                {
                  id: nanoid(),
                  actionName: values.action,
                  expectedDuration: moment(values.duration).format(format),
                  isWork: values.category === ActionType.Work ? true : false,
                  date: currentDate,
                  realDuration: 0,
                  emotion: 0,
                  isDone: false,
                },
              ]);

              store.store.addActionCard({
                id: nanoid(),
                actionName: values.action,
                expectedDuration: moment(values.duration).format(format),
                isWork: values.category === ActionType.Work ? true : false,
                date: currentDate,
                realDuration: 0,
                emotion: "",
                isDone: false,
              });
              form.resetFields();
            }}
          >
            <Form.Item
              name="action"
              label={
                <label
                  style={{
                    color: "#023047",
                    fontFamily: "PTSansRegular",
                    fontWeight: "bold",
                  }}
                >
                  Plans
                </label>
              }
              rules={[{ required: true, message: "To be inserted" }]}
            >
              <Input
                placeholder="Insert your task"
                style={{
                  width: "150px",
                  border: "0.5px solid #023047",
                }}
              />
            </Form.Item>
            <Form.Item
              name="duration"
              label={
                <label
                  style={{
                    color: "#023047",
                    fontFamily: "PTSansRegular",
                    fontWeight: "bold",
                  }}
                >
                  Duration
                </label>
              }
              rules={[
                {
                  required: true,
                  message: "To be inserted",
                },
                ({ getFieldValue }) => ({
                  validator(_, value) {
                    if (moment(value).format(format) === "00:00") {
                      return Promise.reject(
                        "Please enter duration of the task"
                      );
                    } else {
                      return Promise.resolve();
                    }
                  },
                }),
              ]}
            >
              <TimePicker
                placeholder="hh:mm"
                format={format}
                style={{
                  width: "100px",
                  border: "0.5px solid #023047",
                }}
                showNow={false}
              />
            </Form.Item>
            <Form.Item
              name="category"
              label={
                <label
                  style={{
                    color: "#023047",
                    fontFamily: "PTSansRegular",
                    fontWeight: "bold",
                  }}
                >
                  Category
                </label>
              }
              rules={[{ required: true, message: "To be inserted" }]}
            >
              <Select
                placeholder="Select"
                optionFilterProp="children"
                style={{
                  width: "90px",
                  border: "0.5px solid #023047",
                }}
              >
                <Option value={ActionType.Work}>{ActionType.Work}</Option>
                <Option value={ActionType.Rest}>{ActionType.Rest}</Option>
              </Select>
            </Form.Item>
            <Form.Item name="saveButton">
              <Button
                htmlType="submit"
                style={{
                  backgroundColor: "rgba(255, 183, 3, 0.8)",
                  fontFamily: "PTSansRegular",
                  color: "#023047",
                  fontWeight: "bold",
                  // border: "1px solid #023047",
                }}
              >
                Save
              </Button>
            </Form.Item>
          </Form>
        </div>
      </div>
      <MemoryOverlay />
    </>
  );
});

export default Planning;
