import { makeAutoObservable } from "mobx";
import moment from "moment";
import "moment/locale/ru";
import axios from "axios";
export const memoryLocalStorageKey = "memory";
export const tokenLocalStorageKey = "token";
export enum ActionType {
  Work = "Work",
  Rest = "Leisure",
}

export interface ActionCard {
  id: string;
  actionName: string;
  expectedDuration: string;
  isWork: boolean;
  date: string;
  realDuration: number;
  emotion: string;
  isDone: boolean;
}

export interface ServerActionCard {
  ActionName: string;
  ExpectedDuration: string;
  IsWork: string;
  Date: string;
  RealDuration: number;
  Emotion: string;
  IsDone: string;
  Id: string;
}

export default interface Store {
  actionCards: Array<ActionCard>;
  profile: string;
  setProfile(name: string): void;
  getActionCards(): Promise<void>;
  getUsername(): Promise<void>;
  addActionCard(actionCard: ActionCard): void;
  preparePlanningChart(date: string): number[];
  prepareDonePlansChart(date: string): number[];
  actionCardsForSomePeriod(days: number): ActionCard[];
  sortPlanner(date: string): ActionCard[];
  changeRealDuration(id: string, time: number): void;
  changeEmotion(id: string, value: string): void;
  changeActionName(id: string, val: string): void;
  changeExpectedDuration(id: string, val: string): void;
  changeIsWork(id: string, val: boolean): void;
  removeActionCard(id: string): void;
  removeDatedActionCards(date: string): void;
  removeAllActionCards(): void;
  completeActionCard(id: string): void;
  completeFinilisedActions(id: string): void;
  updateActionCard(id: string): void;
}

export const LOCALSTORAGE_KEY = "data";

export class ServerStore implements Store {
  actionCards: Array<ActionCard> = [];
  profile = "";

  constructor() {
    makeAutoObservable(this);
  }

  setProfile(name: string) {
    this.profile = name;
  }

  async getActionCards() {
    const res = await axios.get("/get_todo", {
      headers: { Token: localStorage.getItem("token") || "" },
    });
    const serverCards = res.data;
    if (res.data === null) {
      this.actionCards = [];
      return;
    } else {
      serverCards.map((card: ServerActionCard) => {
        this.actionCards.push({
          id: card.Id,
          actionName: card.ActionName,
          expectedDuration: card.ExpectedDuration,
          isWork: card.IsWork === "true" ? true : false,
          date: card.Date,
          realDuration: card.RealDuration,
          emotion: card.Emotion,
          isDone: card.IsDone === "true" ? true : false,
        });
        return this.actionCards;
      });
    }
  }

  async getUsername() {
    const res = await axios.get("/get_username", {
      headers: { Token: localStorage.getItem("token") || "" },
    });
    this.profile = res.data;
  }

  addActionCard(actionCard: ActionCard) {
    this.actionCards.push(actionCard);
    const card = {
      actionName: actionCard.actionName,
      expectedDuration: actionCard.expectedDuration,
      isWork: actionCard.isWork.toString(),
      date: actionCard.date,
      realDuration: actionCard.realDuration.toString(),
      emotion: actionCard.emotion,
      isDone: actionCard.isDone.toString(),
      id: actionCard.id,
    };
    axios
      .post("/create_todo", card, {
        headers: { Token: localStorage.getItem("token") || "" },
      })
      .then((response) =>
        this.actionCards.map((action) =>
          action.id === actionCard.id
            ? { ...action, id: response.data }
            : action
        )
      );
  }

  updateActionCard(id: string) {
    const updatedCard = this.actionCards.filter(
      (actionCard) => actionCard.id === id
    );

    const card = {
      id: updatedCard[0].id,
      actionName: updatedCard[0].actionName,
      expectedDuration: updatedCard[0].expectedDuration,
      isWork: updatedCard[0].isWork.toString(),
      date: updatedCard[0].date,
      realDuration: updatedCard[0].realDuration.toString(),
      emotion: updatedCard[0].emotion,
      isDone: updatedCard[0].isDone.toString(),
    };

    axios
      .post("/update_todo", card, {
        headers: { Token: localStorage.getItem("token") || "" },
      })
      .catch((_) => console.log("Error"));
  }

  preparePlanningChart(date: string) {
    const workActionCards = this.sortPlanner(date).filter(
      (actionCard) => actionCard.isWork === true
    );
    const notWorkActionCards = this.sortPlanner(date).filter(
      (actionCard) => actionCard.isWork === false
    );

    let totalMins1 = 0;
    workActionCards.map((actionCard) => {
      const arr = actionCard.expectedDuration.split(":");
      totalMins1 += +arr[0] * 60 + +arr[1];
      return totalMins1;
    });

    let totalMins2 = 0;
    notWorkActionCards.map((actionCard) => {
      const arr = actionCard.expectedDuration.split(":");
      totalMins2 += +arr[0] * 60 + +arr[1];
      return totalMins2;
    });

    let background = 0;
    if (totalMins1 === 0 && totalMins2 === 0) {
      background = 100;
    }

    const finalArr = [totalMins1, totalMins2, background];
    return finalArr;
  }

  prepareDonePlansChart(date: string) {
    let expectedDur = 0;
    let realDur = 0;
    this.sortPlanner(date).map((action) => {
      const expectedDurArr = action.expectedDuration.split(":");
      expectedDur += +expectedDurArr[0] * 3600 + +expectedDurArr[1] * 60;
      realDur += action.realDuration;
      return expectedDur && realDur;
    });
    let unDone = 100;
    let done = (realDur * 100) / expectedDur;
    if (realDur !== 0 || done > 1) {
      unDone = 100 - done;
    }
    return [done, unDone];
  }

  actionCardsForSomePeriod(days: number) {
    const today = moment();
    const before = today.clone().subtract(days, "days");
    const filteredArr = this.actionCards.filter((actionCard) => {
      return (
        moment(actionCard.date).isSameOrAfter(before, "day") &&
        moment(actionCard.date).isSameOrBefore(today, "day")
      );
    });
    return filteredArr;
  }

  changeRealDuration(id: string, time: number) {
    this.actionCards = this.actionCards.map((actionCard) =>
      actionCard.id === id
        ? { ...actionCard, realDuration: actionCard.realDuration + time }
        : actionCard
    );
  }

  changeEmotion(id: string, value: string) {
    this.actionCards = this.actionCards.map((actionCard) =>
      actionCard.id === id ? { ...actionCard, emotion: value } : actionCard
    );
    this.updateActionCard(id);
  }

  changeActionName(id: string, val: string) {
    this.actionCards = this.actionCards.map((actionCard) =>
      actionCard.id === id ? { ...actionCard, actionName: val } : actionCard
    );
  }

  changeExpectedDuration(id: string, val: string) {
    this.actionCards = this.actionCards.map((actionCard) =>
      actionCard.id === id
        ? { ...actionCard, expectedDuration: val }
        : actionCard
    );
  }

  changeIsWork(id: string, val: boolean) {
    this.actionCards = this.actionCards.map((actionCard) =>
      actionCard.id === id ? { ...actionCard, isWork: val } : actionCard
    );
  }

  removeActionCard(id: string) {
    const removedCard = { id: id };
    axios
      .post("/remove_todo", removedCard, {
        headers: { Token: localStorage.getItem("token") || "" },
      })
      .then((response) => {
        this.actionCards = this.actionCards.filter(
          (action) => action.id !== id
        );
      });
  }

  removeDatedActionCards(date: string) {
    const message = { date: date };
    axios
      .post("/delete_dated_todos", message, {
        headers: { Token: localStorage.getItem("token") || "" },
      })
      .then((response) => {
        this.actionCards = this.actionCards.filter(
          (action) => action.date !== date
        );
      });
  }

  removeAllActionCards() {
    const message = "remove all todos";
    axios
      .post("/delete_all_todos", message, {
        headers: { Token: localStorage.getItem("token") || "" },
      })
      .catch((_) => console.log("Error"));
    this.getActionCards();
  }

  completeActionCard(id: string) {
    this.actionCards = this.actionCards.map((actionCard) =>
      actionCard.id === id
        ? { ...actionCard, isDone: !actionCard.isDone }
        : actionCard
    );
    this.updateActionCard(id);
  }

  completeFinilisedActions(id: string) {
    const finilisedActionCards = this.actionCards.filter(
      (actionCard) => actionCard.id === id
    );
    let cards: {
      actionName: string;
      expectedDuration: string;
      isWork: string;
      date: string;
      realDuration: string;
      emotion: string;
      isDone: string;
    }[] = [];
    finilisedActionCards.map((finilisedCard) => {
      cards.push({
        actionName: finilisedCard.actionName,
        expectedDuration: finilisedCard.expectedDuration,
        isWork: finilisedCard.isWork.toString(),
        date: finilisedCard.date,
        realDuration: finilisedCard.realDuration.toString(),
        emotion: finilisedCard.emotion,
        isDone: finilisedCard.isDone.toString(),
      });
      return cards;
    });
    const json = JSON.stringify(cards);
    axios.post("/complete_finilisedtodos", json);

    this.actionCards = this.actionCards.map((actionCard) =>
      actionCard.id === id ? { ...actionCard, isDone: true } : actionCard
    );
  }

  sortPlanner(date: string) {
    const sortedCards = this.actionCards.filter(
      (actionCard) => actionCard.date === date
    );
    return sortedCards;
  }

  changeCard(card: ActionCard) {
    const updatedCard = {
      actionName: card.actionName,
      expectedDuration: card.expectedDuration,
      isWork: card.isWork.toString(),
      date: card.date,
      realDuration: card.realDuration.toString(),
      emotion: card.emotion,
      isDone: card.isDone.toString(),
    };
    const json = JSON.stringify(updatedCard);
    axios.post("/change_todo", json);
  }
}

export class LocalStorageStore implements Store {
  actionCards: Array<ActionCard> = [];
  profile = "";

  constructor() {
    makeAutoObservable(this);
  }
  setProfile(name: string) {
    this.profile = name;
  }

  async getActionCards() {
    if (localStorage.getItem(LOCALSTORAGE_KEY) === null) {
      this.actionCards = [];
    } else {
      const data = JSON.parse(localStorage.getItem(LOCALSTORAGE_KEY)!).actions;
      this.actionCards = data;
    }
  }

  async getUsername() {
    this.profile = "";
  }
  addActionCard(actionCard: ActionCard) {
    this.actionCards.push(actionCard);
    this.save();
  }

  updateActionCard(id: string) {
    this.save();
  }

  preparePlanningChart(date: string) {
    const workActionCards = this.sortPlanner(date).filter(
      (actionCard) => actionCard.isWork === true
    );
    const notWorkActionCards = this.sortPlanner(date).filter(
      (actionCard) => actionCard.isWork === false
    );

    let totalMins1 = 0;
    workActionCards.map((actionCard) => {
      const arr = actionCard.expectedDuration.split(":");
      totalMins1 += +arr[0] * 60 + +arr[1];
      return totalMins1;
    });

    let totalMins2 = 0;
    notWorkActionCards.map((actionCard) => {
      const arr = actionCard.expectedDuration.split(":");
      totalMins2 += +arr[0] * 60 + +arr[1];
      return totalMins2;
    });

    let background = 0;
    if (totalMins1 === 0 && totalMins2 === 0) {
      background = 100;
    }

    const finalArr = [totalMins1, totalMins2, background];
    return finalArr;
  }

  prepareDonePlansChart(date: string) {
    let expectedDur = 0;
    let realDur = 0;
    this.sortPlanner(date).map((action) => {
      const expectedDurArr = action.expectedDuration.split(":");
      expectedDur += +expectedDurArr[0] * 3600 + +expectedDurArr[1] * 60;
      realDur += action.realDuration;
      return expectedDur && realDur;
    });
    let unDone = 100;
    let done = (realDur * 100) / expectedDur;
    if (realDur !== 0 || done > 1) {
      unDone = 100 - done;
    }
    return [done, unDone];
  }

  actionCardsForSomePeriod(days: number) {
    const today = moment();
    const before = today.clone().subtract(days, "days");
    const filteredArr = this.actionCards.filter((actionCard) => {
      return (
        moment(actionCard.date).isSameOrAfter(before, "day") &&
        moment(actionCard.date).isSameOrBefore(today, "day")
      );
    });
    return filteredArr;
  }

  changeRealDuration(id: string, time: number) {
    this.actionCards = this.actionCards.map((actionCard) =>
      actionCard.id === id
        ? { ...actionCard, realDuration: actionCard.realDuration + time }
        : actionCard
    );
    this.save();
  }

  changeEmotion(id: string, value: string) {
    this.actionCards = this.actionCards.map((actionCard) =>
      actionCard.id === id ? { ...actionCard, emotion: value } : actionCard
    );
    this.save();
  }

  changeActionName(id: string, val: string) {
    this.actionCards = this.actionCards.map((actionCard) =>
      actionCard.id === id ? { ...actionCard, actionName: val } : actionCard
    );
    this.save();
  }

  changeExpectedDuration(id: string, val: string) {
    this.actionCards = this.actionCards.map((actionCard) =>
      actionCard.id === id
        ? { ...actionCard, expectedDuration: val }
        : actionCard
    );
    this.save();
  }

  changeIsWork(id: string, val: boolean) {
    this.actionCards = this.actionCards.map((actionCard) =>
      actionCard.id === id ? { ...actionCard, isWork: val } : actionCard
    );
    this.save();
  }

  removeActionCard(id: string) {
    this.actionCards = this.actionCards.filter(
      (actionCard) => actionCard.id !== id
    );
    this.save();
  }

  removeDatedActionCards(date: string) {
    this.actionCards = this.actionCards.filter(
      (actionCard) => actionCard.date !== date
    );
    this.sortPlanner(date);
    this.save();
  }

  removeAllActionCards() {
    this.actionCards = [];
    this.clearData();
  }

  completeActionCard(id: string) {
    this.actionCards = this.actionCards.map((actionCard) =>
      actionCard.id === id
        ? { ...actionCard, isDone: !actionCard.isDone }
        : actionCard
    );
    this.save();
  }

  completeFinilisedActions(id: string) {
    this.actionCards = this.actionCards.map((actionCard) =>
      actionCard.id === id ? { ...actionCard, isDone: true } : actionCard
    );
    this.save();
  }

  sortPlanner(date: string) {
    const sortedCards = this.actionCards.filter(
      (actionCard) => actionCard.date === date
    );
    return sortedCards;
  }

  save() {
    const savedData = { actions: this.actionCards };
    localStorage.setItem(LOCALSTORAGE_KEY, JSON.stringify(savedData));
  }

  clearData() {
    localStorage.clear();
  }
}

export interface SavedData {
  actions: ActionCard[];
}
