import "chart.js/auto";
import { Chart } from "react-chartjs-2";
import { Card } from "antd";
import store from "./Store2";
import { observer } from "mobx-react-lite";

function EmotionChart({ date }: { date: string }) {
  let stringEmotion;
  function prepareEmotionChart(date: string) {
    const badEmotionCards = store.store
      .sortPlanner(date)
      .filter((actionCard) => actionCard.emotion === "");
    const middleEmotionCards = store.store
      .sortPlanner(date)
      .filter((actionCard) => actionCard.emotion === "");
    const goodEmotionCards = store.store
      .sortPlanner(date)
      .filter((actionCard) => actionCard.emotion === "");

    const badEmotion = badEmotionCards.reduce((acc, val) => {
      return acc + val.realDuration / 60;
    }, 0);

    const middleEmotion = middleEmotionCards.reduce((acc, val) => {
      return acc + val.realDuration / 60;
    }, 0);

    const goodEmotion = goodEmotionCards.reduce((acc, val) => {
      return acc + val.realDuration / 60;
    }, 0);

    let background = 0;
    if (badEmotion === 0 && middleEmotion === 0 && goodEmotion === 0) {
      background = 100;
    }

    const arr = [goodEmotion, middleEmotion, badEmotion, background];

    if (goodEmotion > middleEmotion && goodEmotion > badEmotion) {
      stringEmotion = "excellent";
    } else if (middleEmotion > goodEmotion && middleEmotion > badEmotion) {
      stringEmotion = "good";
    } else if (badEmotion > goodEmotion && badEmotion > middleEmotion) {
      stringEmotion = "badly";
    }

    return arr;
  }
  const data = prepareEmotionChart(date);

  const chartData = {
    labels: [" (min)", " (min)", " (min)"],
    datasets: [
      {
        id: 1,
        label: `${store.store.actionCards.length}`,
        data,
        backgroundColor: [
          "rgba(167, 201, 87, 1)",
          "rgba(255, 223, 82, 1)",
          "rgba(188, 71, 73, 1)",
          "rgba(243, 246, 224, 0.5)",
        ],
        borderColor: ["black", "black", "black"],
        borderWidth: 0.2,
      },
    ],
  };

  return (
    <div>
      <Card
        style={{
          backgroundColor: "#023047",
          color: "rgba(255, 183, 3)",
          fontWeight: "bold",
          textAlign: "center",
          height: "354px",
        }}
      >
        <Chart
          datasetIdKey={`${store.store.actionCards.length}`}
          type="pie"
          data={chartData}
          options={{
            color: "rgba(255, 183, 3)",
            font: {
              family: "PTSansRegular",
            },
          }}
        />
        <p>Mostly completing tasks you felt {stringEmotion}</p>
      </Card>
    </div>
  );
}

export default observer(EmotionChart);
