import Store, {
  LocalStorageStore,
  memoryLocalStorageKey,
  tokenLocalStorageKey,
} from "./Store";
import { makeAutoObservable } from "mobx";

class Store2 {
  store: Store = new LocalStorageStore();
  showOverlay = false;
  constructor() {
    makeAutoObservable(this);
  }

  setStore(store: Store) {
    this.store = store;
  }

  setShowOverlay(_: boolean) {
    this.showOverlay = _;
  }

  clearStore() {
    localStorage.removeItem(memoryLocalStorageKey);
    localStorage.removeItem(tokenLocalStorageKey);
    this.store = new LocalStorageStore();
  }
}
const store = new Store2();
export default store;
