import { Menu, MenuItem, Button } from "@blueprintjs/core";
import store from "./Store2";
import { UserOutlined } from "@ant-design/icons";
import { observer } from "mobx-react-lite";
import { Dropdown } from "antd";

const Profile = observer(() => {
  const exampleMenu = (
    <Menu>
      <MenuItem
        key="1"
        icon="log-out"
        text="Log out"
        onClick={() => {
          store.store.setProfile("");
          store.clearStore();
          store.setShowOverlay(true);
        }}
      />
    </Menu>
  );

  if (store.store.profile === "") {
    return (
      <Button
        minimal={true}
        className="menu_link"
        onClick={() => {
          store.clearStore();
          store.setShowOverlay(true);
        }}
      >
        Log in
      </Button>
    );
  } else {
    return (
      <div className="profile">
        <Dropdown overlay={exampleMenu}>
          <Button minimal={true} style={{ color: "#023047" }}>
            <UserOutlined /> {store.store.profile}
          </Button>
        </Dropdown>
      </div>
    );
  }
});

export default Profile;
