import { useState, useEffect } from "react";
import store from "./Store2";
import { LocalStorageStore, ServerStore, memoryLocalStorageKey } from "./Store";
import axios from "axios";
import { observer } from "mobx-react-lite";
import { Modal, Button, Input, Space } from "antd";

const MemoryOverlay = observer(() => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [regUsername, setRegUsername] = useState("");
  const [regPassword, setRegPassword] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const [errorMessageAuth, setErrorMessageAuth] = useState("");
  const [showRegForm, setShowRegForm] = useState(false);

  const sendLoginForm = function () {
    const userCard = {
      username: username,
      password: password,
    };

    const json = JSON.stringify(userCard);

    axios
      .post("/login", json)
      .then((response) => {
        if (response.data === "failed") {
          setErrorMessageAuth(
            "Your login or (and) password are incorrect. Try again."
          );
          setUsername("");
          setPassword("");
          return;
        }
        localStorage.setItem(memoryLocalStorageKey, "server");
        const token = response.data;
        const localStorageKey = "token";
        localStorage.setItem(localStorageKey, token);
        store.setStore(new ServerStore());
        store.store.getUsername();
        store.store.getActionCards();
        setShowRegForm(false);
      })
      .catch((err) => {
        setErrorMessageAuth("Server does not respond");
        setUsername("");
        setPassword("");
      });
  };

  function register() {
    const userCard = {
      username: regUsername,
      password: regPassword,
    };
    const json = JSON.stringify(userCard);

    axios
      .post("/register", json)
      .then((response) => {
        if (response.data === "failed") {
          setErrorMessage(
            "Inserted login is aleady taken. Please try another one."
          );
          setRegUsername("");
          setRegPassword("");
          return;
        }
        localStorage.setItem(memoryLocalStorageKey, "server");
        const token = response.data;
        const localStorageKey = "token";
        localStorage.setItem(localStorageKey, token);
        store.setStore(new ServerStore());
        store.store.getUsername();
        store.store.getActionCards();
        setShowRegForm(false);
      })
      .catch((err) => {
        if (err.response.status === 500) {
          setErrorMessage("Server does not respond.");
          setRegUsername("");
          setRegPassword("");
        } else {
          throw err;
        }
      });
  }

  useEffect(() => {
    if (localStorage.getItem(memoryLocalStorageKey) === "localStorage") {
      store.setStore(new LocalStorageStore());
      store.setShowOverlay(false);
      store.store.getActionCards();
    } else if (localStorage.getItem(memoryLocalStorageKey) === "server") {
      if (localStorage.getItem("token") !== "") {
        store.setStore(new ServerStore());
        store.store.getUsername();
        store.setShowOverlay(false);
        store.store.getActionCards();
      }
    } else {
      store.setShowOverlay(true);
    }
  }, []);

  return (
    <>
      <Modal
        visible={store.showOverlay}
        centered={true}
        closable={false}
        maskClosable={false}
        footer={null}
        style={{
          textAlign: "center",
        }}
      >
        <h1 style={{ color: "#023047" }}>
          By default, your tasks are stored in LocalStorage. But you can choose
          to store the data on the server.
        </h1>
        <Button
          className="login_button"
          key="localStorage"
          onClick={() => {
            localStorage.setItem("memory", "localStorage");
            store.setStore(new LocalStorageStore());
            store.store.getActionCards();
            store.setShowOverlay(false);
          }}
        >
          Store in LocalStorage
        </Button>
        <Button
          key="server"
          onClick={() => {
            store.setShowOverlay(false);
            setShowRegForm(true);
          }}
          className="login_button"
        >
          Store on the server
        </Button>
      </Modal>
      <Modal
        visible={showRegForm}
        centered={true}
        closable={false}
        maskClosable={false}
        footer={null}
        style={{
          textAlign: "center",
        }}
      >
        <Space direction="vertical">
          <h1 className="login_text">
            Please login to save your data on the server:
          </h1>
          <Input
            defaultValue={username}
            placeholder="Login"
            onChange={(event) => setUsername(event.target.value)}
            className="login_input"
          />
          <Input
            defaultValue={password}
            type="password"
            placeholder="Password"
            onChange={(event) => setPassword(event.target.value)}
            className="login_input"
          />
          <Button onClick={sendLoginForm} className="login_button">
            Login
          </Button>
          <p className="text">{errorMessageAuth}</p>
          <h1 className="login_text">
            If you don't have an account, please, register:
          </h1>
          <Input
            defaultValue={regUsername}
            type="text"
            placeholder="Login"
            onChange={(event) => setRegUsername(event.target.value)}
            className="login_input"
          />
          <Input
            defaultValue={regPassword}
            type="password"
            placeholder="Password"
            onChange={(event) => setRegPassword(event.target.value)}
            className="login_input"
          />
          <Button className="login_button" onClick={register}>
            Register
          </Button>
          <p className="memoryRequest_form_error">{errorMessage}</p>
          <Button
            className="login_button"
            onClick={() => {
              localStorage.setItem("memory", "localStorage");
              store.setStore(new LocalStorageStore());
              store.setShowOverlay(false);
              setShowRegForm(false);
            }}
          >
            Save data in LocalStorage
          </Button>
        </Space>
      </Modal>
    </>
  );
});

export default MemoryOverlay;
