import { observer } from "mobx-react-lite";
import { Table, Select, Button } from "antd";
import "./App.css";
import { ActionType } from "./Store";
import store from "./Store2";
import moment from "moment";
import Clock from "./Clock";
import "moment/locale/ru";

const ActionList = observer(() => {
  const currentDate = moment().format("YYYY-MM-DD");
  const { Option } = Select;
  const list = store.store.actionCards.filter(
    (actionCard) => actionCard.date === currentDate
  );

  const columns = [
    {
      title: "Your plans:",
      dataIndex: "plans",
      key: "plans",
    },
    {
      title: "Category:",
      dataIndex: "category",
      key: "category",
    },
    {
      title: "Stopwatch:",
      dataIndex: "stopwatch",
      key: "stopwatch",
    },
    {
      title: "Mood:",
      dataIndex: "emotion",
      key: "emotion",
    },
    {
      title: "",
      dataIndex: "remove",
      key: "remove",
    },
  ];

  const data = list.map((card) => {
    return {
      key: card.id,
      plans: (
        <div
          style={
            card.isWork
              ? {
                  width: "160px",
                  backgroundColor: "rgba(142, 202, 230)",
                }
              : {
                  width: "160px",
                  backgroundColor: "rgba(255, 183, 3)",
                }
          }
        >
          {card.actionName + ` (${card.expectedDuration})`}
        </div>
      ),
      category: <div>{card.isWork ? ActionType.Work : ActionType.Rest}</div>,
      stopwatch: (
        <Clock
          id={card.id}
          duration={card.realDuration}
          isButtonDisabled={card.isDone || false}
        />
      ),
      emotion: (
        <Select
          defaultValue=""
          disabled={card.isDone || false}
          onChange={(value) => store.store.changeEmotion(card.id, value)}
        >
          <Option value=""></Option>
          <Option value=""></Option>
          <Option value=""></Option>
        </Select>
      ),
      remove: (
        <Button onClick={() => store.store.completeActionCard(card.id)}>
          {card.isDone ? "Cancel" : "Finish"}
        </Button>
      ),
    };
  });

  if (list.length === 0) {
    return (
      <div className="mainBlock2">
        <h4 className="noTasks">No plans for today.</h4>
      </div>
    );
  } else {
    return (
      <Table
        style={{ color: "#023047" }}
        columns={columns}
        dataSource={data}
        size="small"
        bordered={false}
        pagination={{
          pageSize: 4,
        }}
      />
    );
  }
});

export default ActionList;
