import { useState } from "react";
import { Menu, Dropdown } from "antd";
import "./App.css";
import WeeklyReport from "./WeeklyReport";
import MemoryOverlay from "./MemoryOverlay";

enum ReportType {
  Weekly = "last week",
  Monthly = "last month",
  Annually = "last year",
}

function Statistics() {
  const [statistics, setStatistics] = useState("last week");
  let days = 7;

  if (statistics === ReportType.Monthly) {
    days = 30;
  } else if (statistics === ReportType.Annually) {
    days = 365;
  }

  const menu = (
    <Menu>
      <Menu.Item key="0">
        <p
          className="statistics_text"
          onClick={() => setStatistics(ReportType.Weekly)}
        >
          {ReportType.Weekly}
        </p>
      </Menu.Item>
      <Menu.Item key="1">
        <p
          className="statistics_text"
          onClick={() => setStatistics(ReportType.Monthly)}
        >
          {ReportType.Monthly}
        </p>
      </Menu.Item>
      <Menu.Item key="2">
        <p
          className="statistics_text"
          onClick={() => setStatistics(ReportType.Annually)}
        >
          {ReportType.Annually}
        </p>
      </Menu.Item>
    </Menu>
  );

  return (
    <>
      <div className="statistics_range">
        <div className="statistics_text_container">
          <h1 className="statistics_text">
            Analyze your results for the{" "}
            <Dropdown overlay={menu}>
              <span className="statistics_text statistics_link">
                {statistics}
              </span>
            </Dropdown>
          </h1>
        </div>
      </div>
      <WeeklyReport days={days} />
      <MemoryOverlay />
    </>
  );
}
export default Statistics;
